<template>
    <div class="apply-withdrawal" v-loading="loading">
		<vue-scroll>
			<el-form :model="applyForm" :rules="applyRules" :show-message="false" class="form-data">
				<div class="apply" v-if="tableData && tableData.hasOwnProperty('type') && tableData.type === 0">
					<p class="title">{{ $L('可提现佣金') }}{{tableData.accountCan}}</p>
					<ul class="ul-box">
						<li class="li-item">
							<div class="left">{{ $L('提现金额') }}：</div>
							<div class="right">
								<el-input size="small" v-model="withdrawalMoney" max="tableData.accountCan"></el-input>
							</div>
						</li>
						<li class="li-item">
							<div class="left">{{ $L(' 提现方式') }}：</div>
							<div class="right" v-if="tableData && tableData.hasOwnProperty('payType')">
								<el-select
										size="small"
										v-model="withdrawalType"
										:placeholder="$L('请选择活动区域')"
										@change="selectChange">
									<el-option
											:value="i.payName"
											v-for="i in tableData.payType"
											:key="i.payId"
									>{{ i.payName }}</el-option>
								</el-select>
							</div>
						</li>
						<li class="li-item">
							<div class="left">{{ $L(' 最小提现额') }}：</div>
							<div class="right">{{ payFeeAccount }}</div>
						</li>
						<li class="li-item">
							<div class="left">{{ $L(' 提现手续费') }}：</div>
							<div class="right">{{ payFee }}</div>
						</li>
						<li class="li-item">
							<div class="left">{{ $L(' 实际金额') }}：</div>
							<div class="right">
								{{ actualFeeAccount > 0 ? actualFeeAccount : 0 }}{{ $L(' 元') }}
							</div>
						</li>
						<li class="li-item li-two">
							<div class="left">{{ $L(' 提现说明') }}：</div>
							<div class="right">
								<span v-if="note">{{ note }}</span>
								<span v-else>{{ $L('无') }}</span>
							</div>
						</li>
						<li class="li-item">
							<div class="left">{{ $L('收款账号') }}：</div>
							<div class="right">
								<el-form-item prop="account">
									<el-input size="small" v-model="applyForm.account"></el-input>
								</el-form-item>
							</div>
						</li>
						<li class="li-item">
							<div class="left">{{ $L('账户姓名') }}：</div>
							<div class="right">
								<el-form-item prop="accountOwner">
									<el-input size="small" v-model="applyForm.accountOwner"></el-input>
								</el-form-item>
							</div>
						</li>
						<li class="li-item">
							<div class="left">{{ $L('联系电话') }}：</div>
							<div class="right">
								<el-form-item prop="mobile">
									<el-input size="small" v-model="applyForm.mobile"></el-input>
								</el-form-item>
							</div>
						</li>
					</ul>
				</div>
				<div class="apply apply-success" v-if="tableData && tableData.hasOwnProperty('type') && tableData.type === 1">
					<p class="title">{{ $L('可提现佣金') }}： {{ tableData.accountCan }}</p>
					<div class="tips">
						<span class="bbx-font bbx-icon-gantanhao mainFontColor"></span>
						<p class="font-big">
							{{ $L('可提现佣金满') }}
							<span v-if="tableData.min">{{ tableData.min }}</span>
							<span class="mainFontColor" v-else>{{ $L('未设置') }}</span>，{{ $L('全部自动返佣到') }}
							<span v-if="tableData.payName">{{ tableData.payName }}</span>
							<span class="mainFontColor" v-else>{{ $L('至后台设置自动返佣方式') }}</span>，
							<span v-if="tableData.fee">{{ $L('提现手续费') }}{{ tableData.fee }} ，{{ $L('预计到账佣金') }}{{ tableData.actualMoney }}</span>
							<span v-else></span>
						</p>
						<p class="note">{{ $L('备注') }} ：{{ tableData.note }}</p>
					</div>
				</div>
			</el-form>
			<div class="bottom-btns" v-if="tableData && tableData.hasOwnProperty('type') && tableData.type === 0">
			    <el-button class="btn" size="small" type="primary" @click="applyFunc">{{ $L('申请') }}</el-button>
			</div>
		</vue-scroll>
    </div>
</template>

<script>
    export default {
        name: "applyWithdrawal",
        data () {
            return {
                value: '',
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                loading:true,
                tableData:{},
                withdrawalMoneyData:'',
                withdrawalType:'',      //提现方式
                discount:0,
                payFee: 0,
                payFeeAccount: 0,
                actualFeeAccount:0,
                note: '无',
                payId:'',
                applyForm:{
                    mobile:'',
                    account:'',
                    accountOwner:''
                },
                applyRules:{
                    mobile: [
                        {
                            validator: (rule, value, callback) => {
                                value = parseInt(value);
                                if(isNaN(value)){
                                    callback(new Error('请选输入正确的手机号码或固定电话'));
                                }else{
                                    let patter = /^((0\d{2,3}-\d{7,8})|(1[3456789]\d{9}))$/;
                                    if(!patter.test(value)){
                                        return callback(new Error('手机号码或固定电话有误'));
                                    } else {
                                        callback();
                                    }

                                }
                            }
                        },
                    ],
                    account:[
                        {
                            validator: (rule, value, callback) => {
                                value = parseInt(value);
                                if(isNaN(value)){
                                    callback(new Error('请选输入正确的账号'));
                                }else{
                                    let patter = /^[0-9]\d{16,19}$/;
                                    if(!patter.test(value)){
                                        return callback(new Error('账号有误'));
                                    } else {
                                        callback();
                                    }

                                }
                            }
                        }
                    ]
                }
            }
        },
        computed: {
            withdrawalMoney: {
                get() {
                    return this.withdrawalMoneyData;
                },
                set(val) {
                    val = parseFloat(val);
                    if (isNaN(val)) {
                        val = this.withdrawalMoneyData;
                    }
                    this.withdrawalMoneyData = val;
                }
            }
        },
        created () {
            this.getData();
        },
        watch: {
            withdrawalMoney:function () {
            	console.log(this.withdrawalMoney);
                if (this.payFee.indexOf('%') >= 0) {
                    this.discount=this.payFee.replace("%","")
                    this.discount= this.discount/100;
                    this.actualFeeAccount = this.withdrawalMoney - this.withdrawalMoney*this.discount;
                } else {
                    this.actualFeeAccount = this.withdrawalMoney - this.payFee;
                }
            }
        },
        methods : {
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('withdrawDeposit', {} , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                        if(!_self.tableData.hasOwnProperty('payType') || !_self.tableData.payType.payName && _self.tableData.type == 1){
                            _self.$alert(_self.$L('暂无提现方式！请联系我司！'), _self.$L('抱歉'), {
                                confirmButtonText: _self.$L('返回'),
                                callback: action => {
                                    _self.$router.push({name:'distributorMain'});
                                }
                            });
                            _self.loading = true;
                        }else if(_self.tableData.type == 0){
                            _self.withdrawalType = _self.tableData.payType[0].payName;
                            let arr = _self.tableData.payType[0];
                            _self.payFee = arr.fee;
                            if (_self.payFee.indexOf('%') >= 0) {
                                _self.discount=_self.payFee.replace("%","")
                                _self.discount= _self.discount/100;
                                _self.actualFeeAccount = _self.withdrawalMoney - _self.withdrawalMoney*_self.discount;
                            } else {
                                _self.actualFeeAccount = _self.withdrawalMoney - _self.payFee;
                            }
                            _self.payFeeAccount = arr.min;
                            _self.note = arr.note;
                            _self.payId = arr.payId;

                            _self.loading = false;
                        }
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
							_self.$router.push({name: 'distributorMain'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
						_self.$router.push({name: 'distributorMain'});
                    });
                });
            },
            backFunc(){
                this.$router.push({name:'distributorList'});
            },
            selectChange(index){
                let arr = {};
                for (var i=0;i<this.tableData.payType.length;i++) {
                    if(index === this.tableData.payType[i].payName){
                        arr = this.tableData.payType[i];
                        this.payFee = arr.fee;
                        if (this.payFee.indexOf('%') >= 0) {
                            this.discount=this.payFee.replace("%","")
                            this.discount= this.discount/100;
                            this.actualFeeAccount = this.withdrawalMoney - this.withdrawalMoney*this.discount;
                        } else {
                            this.actualFeeAccount = this.withdrawalMoney - this.payFee;
                        }
                        this.payFeeAccount = arr.min;
                        this.note = arr.note;
                        this.payId = arr.payId;
                    }
                }
            },
            applyFunc(){
                const _self = this;
                if (Number(_self.tableData.accountCan)>= Number(_self.withdrawalMoney)) {
                    _self.applyFormFunc();
                } else {
                    _self.$message({
                        type: 'error',
                        message: _self.$L('可提现金额不足!')
                    });
                }
            },
            applyFormFunc(){
                const _self = this;
                _self.loading = true;
                let arr = {
                    payId : _self.payId,
                    tobMoney: _self.withdrawalMoney,
                    account: _self.applyForm.account,
                    accountOwner: _self.applyForm.accountOwner,
                    mobile: _self.applyForm.mobile
                };
                if (_self.payId !== '' && _self.withdrawalMoney !== '' && _self.applyForm.account !== '' && _self.applyForm.accountOwner !== '' && _self.applyForm.mobile !=='') {
                    if (Number(_self.payFeeAccount) <= Number(_self.withdrawalMoney)) {
                        _self.$api('withdrawDepositBtn', arr , function (reData) {
                            if (reData.flag === 1) {
                                _self.$message({
                                    type: 'success',
                                    message: _self.$L('申请提现成功!')
                                });
                                _self.loading = false;
                                _self.getData();
                            } else if (reData.flag === -1) {
                                //登录超时了
                                _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                    confirmButtonText: _self.$L('重新登录'),
                                    cancelButtonText: _self.$L('取消'),
                                    type: 'error',
                                    distinguishCancelAndClose: true
                                }).then(() => {
                                    _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                                }).catch(action => {
                                    _self.$gotoWebHome();
                                });
                            } else {
                                //未知错误
                                _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                    confirmButtonText: _self.$L('点击重试'),
                                    cancelButtonText: _self.$L('返回首页'),
                                    type: 'error',
                                    distinguishCancelAndClose: true
                                }).then(() => {
                                    _self.getData();
                                }).catch(action => {
									_self.$router.push({name: 'distributorMain'});
                                });
                            }
                        }, function (error) {
                            //网络请求错误
                            _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
								_self.$router.push({name: 'distributorMain'});
                            });
                        });
                    } else {
                        _self.$message({
                            type: 'info',
                            message: _self.$L('提现金额必须大于等于最小提现金额!')
                        });
                        _self.loading = false;
                    }

                } else {
                    _self.$message({
                        type: 'info',
                        message: _self.$L('必填信息有误,重新输入!')
                    });
                    _self.loading = false;
                }
            }
        }
    }
</script>


<style lang="scss" scoped>
    .apply-withdrawal {
        width: 100%;
        .form-data{
        }
        .apply {
            background: #fff;
            .title {
                font-size: 14px;
                font-weight: 700;
                padding: 10px 0 10px 20px;
                border-bottom: 3px solid #EFF1F5;
            }
            .form {
                /deep/ .el-form-item {
                    margin-bottom: 8px;
                }
                /deep/ .el-input {
                    width: 240px;
                }
                .unit {
                    padding-left: 10px;
                }
                .state {
                    padding-left: 30px;
                }
            }
            .ul-box {
                font-size: 12px;
                .li-item {
                    border-bottom: 1px solid #e1e1e1;
                    padding: 0 20px;
                    height: 50px;
                    line-height: 50px;
					&.li-two{
						height: auto;
						line-height: 20px;
						.right{
							padding: 5px 0;
						}
					}
                    .left {
                        width: 85px;
                        display: inline-block;
                    }
                    .right {
                        width: calc(100% - 85px);
                        display: inline-block;
						vertical-align: middle;
                        .el-input,
                        .el-select {
                            width: 100%;
                        }
                        .unit {
                            padding-left: 10px;
                        }
                        .state {

                        }
                    }
                    .el-form-item{
                        margin-bottom: 0;
                        border: 0;
                    }
                    /deep/ .el-input__inner{
                        border: 0;
                        padding: 0;
                    }
                }
            }
            .tips {
                text-align: center;
                width: 90%;
                margin: 0 auto;
				padding-bottom: 20px;
                .font-big {
                    font-size: 18px;
                }
				.note{
					color: #999;
					padding-top: 10px;
					text-align: left;
				}
                .font-small {
                    padding-top: 10px;
                }
                .bbx-font{
                    font-size: 100px;
                }
            }
        }
        .bottom-btns {
            padding: 10px;
            width: 100%;
            background-color: #fff;
            bottom: 0;
            .btn {
                width: 100%;
                height: 50px;
                font-size: 14px;
            }
        }
    }
</style>
<style>
	@media screen and (max-width:767px) {
		.el-message-box {
			width: 94% !important;
		}
	}
</style>